@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Poppins', sans-serif;
  background-color: rgb(190, 200, 200);  
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

input[type=text] {
  width: 300px;
  padding: 12px 20px;
  margin: 8px 0;
  margin-bottom: 25px;
  box-sizing: border-box;
}

input[type=submit] {
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  margin-left: 20px;
}

.inputmap {
  width: auto;
  border-bottom: 1px solid black;
}

.mapheader{
  padding-top: 12px;
}

.jokulista{
  display: flex;
  justify-content: space-between
}

.mapitem {
  margin-top: 10px;
  margin-left: 20px;
  margin-bottom: 10px;
  list-style-type: 'â€¢';
  padding-inline-start: 1ch;
}

.maincontent{
  width: 530px;
  height: auto;
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 25px;
  padding-bottom: 25px;
  border: 1px solid black;
  margin-bottom: 50px;

}

.navlist {
  list-style-type: none;
  overflow: hidden;
  background-color: #333;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.navlink a {
  padding: 15px 10px 15px 10px;
  display: block;
  color: white;
  text-align: center;
  text-decoration: none;
}

.navlink a:hover {
  background-color: #111;
}

.inputheader {
  padding-bottom: 15px;
}

.inputspan {
  padding-bottom: 200px;
}

.homelist {
  margin-left: 20px;
}

.mapheader {
  margin-top: 5px;
}

.btn-delete {
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  display: inline-block;
  background: rgb(223, 24, 24);
  color: #fff;
  border: none;
  padding: 7px 15px;
  margin-top: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
  margin-right: 10px;
}

.btn-add {
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  display: inline-block;
  background: rgb(9, 178, 51);
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn-clear {
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  display: inline-block;
  background: rgb(216, 20, 20);
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn-draw{
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  display: inline-block;
  background: rgb(104, 101, 101);
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn-count {
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  width: 180px;
  display: inline-block;
  background: rgb(9, 178, 51);
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn-group {
  margin-top: 20px;
  margin-bottom: 20px;
}

.buttonscontent {
  margin-left: 5px;
}

.itemparent{
  border-left: 1px solid black;
  border-bottom: 1px solid black;
  display: flex;
  flex-direction: column;
  padding: 0px 0px 5px 15px;
  margin-bottom: 20px;
  font-size: 13px;
}

.itemimg {
  width: 100%;
}


.game {
  display: flex;
  width: 100%;
  flex-direction: column;
  text-align: center;
  background-color: rgb(190, 200, 200);
  color: black;
  justify-content: center;
  align-items: center;
}

.gametitle {
  font-weight: bold;
  margin-bottom: 25px;
  font-size: 25px;
}

.row {
  display: flex;
  flex-direction: row;
}

.rowjustify {
  justify-content: center;
}

.square {
  width: 100px;
  height: 100px;
  font-size: 64px;
  text-align: center;
  color: #000000;
  font-weight: bolder;
  padding: 30px;
  font-size: 30px;
}

.b-bottom-right {
  border-right: 7px solid black;
  border-bottom: 7px solid black;
}

.b-bottom {
  border-bottom: 7px solid black;
}

.b-right {
  border-right: 7px solid black;
}

.status {
  font-size: 20px;
  margin: 20px 0px 20px 0px;
}

.scoreboard {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 20rem;
  font-size: 1.5em;
  background-color: rgb(229, 234, 243);
  margin: 3rem auto;
  box-shadow: 0px 0px 8px #888;
  border-radius: 0.5rem;
  font-weight: bold;
}

.score {
  width: 100%;
  text-align: center;
  padding: 1rem 0;
}

.x-score{
  color: rgb(204, 74, 74);
  border-bottom: 5px solid rgb(204, 74, 74);
  border-radius: 0.5rem 0 0 0.5rem;
}

.o-score{
  color: rgb(82, 82, 209);
  border-bottom: 5px solid rgb(82, 82, 209);
  border-radius: 0 0.5rem 0.5rem 0;
}

.inactive {
  border-bottom: 5px solid transparent;
}